
* {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    
}

a {
    cursor: pointer;
}


body{
    font-size: initial;
    line-height: initial;
    text-transform: none;
    margin: 0;
    font-family: Verdana;
    /* transition: all 4s; */
    overflow-x: hidden;
    overflow-y: auto;
    /* font-family: Europa,Arial, Helvetica; */
}

html, body {
    /* height: 100%; */
  }

.hide-scroll {
    overflow: hidden;
}

.ps-container {
    position: relative;
    overflow: auto;
}

/* .container {
    width: 100%;
    height: 100vh;
    background-color: white;
    overflow: hidden;
} */

.main-content {
    background-color: transparent;
    margin-left: 60px;
    left: 0px;
    position: relative;
    -webkit-transition: all 0.5s;
    -o-transition: all 0.5s;
    transition: all 0.5s;
}

.main-content-inner{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    min-height: 100vh;
    display: -ms-flexbox;
    display: flex;
}

.project-list{
    width: 100%;
    height: 100%;
    margin-left: 0;
    -webkit-transition: margin-left 0.5s;
    -o-transition: margin-left 0.5s;
    transition: margin-left 0.5s
}

 /****************************************************************/

 /****************************************************************/

 
 @-webkit-keyframes showup {
    from{-webkit-transform: translateY(50%);transform: translateY(50%);}
    to  {-webkit-transform: translateY(0%);transform: translateY(0%);}
}

 
 @keyframes showup {
    from{-webkit-transform: translateY(50%);transform: translateY(50%);}
    to  {-webkit-transform: translateY(0%);transform: translateY(0%);}
}

.copyright{
    opacity: 0.6;
    font-size: 10px;
    margin: 0;
}

#btn-move-top{
    background-color: #242424;
    position: fixed;
    right: 30px;
    bottom: 30px;
    width: 36px;
    height: 36px;
    text-align: center;
    border-radius: 2px;
    cursor: pointer;
    vertical-align: middle;
    z-index: 1;
    padding-left: 11px;
    padding-top: 7px;
    display: none;
}

#btn-move-top a{
    display: table-cell;
    line-height: 0;
    font-size: 20px;
    color: #ffffff;
    vertical-align: middle;
    text-align: center;
}

@media only screen and (max-width: 1224px){
    .nav-vertical{
        display: none;
    }
    .nav-horizontal{
        left: 0;
    }

    .page-footer {
        display: inherit;
    }
    
    .main-content {
        left: 0;
        margin-left: 0;
        margin-top: 80px;
    }

    .project-list{
        margin-left: 0px !important;
    }
    
}






/*************************************************************************/
@keyframes fadeInUp {
    from {
        -webkit-transform: translate3d(0,40px,0);
                transform: translate3d(0,40px,0)
    }

    to {
        -webkit-transform: translate3d(0,0,0);
                transform: translate3d(0,0,0);
        opacity: 1
    }
}

@-webkit-keyframes fadeInUp {
    from {
        -webkit-transform: translate3d(0,40px,0);
                transform: translate3d(0,40px,0)
    }

    to {
        -webkit-transform: translate3d(0,0,0);
                transform: translate3d(0,0,0);
        opacity: 1
    }
}

.animated {
    animation-duration: 1s;
    animation-fill-mode: both;
    -webkit-animation-duration: 1s;
    -webkit-animation-fill-mode: both
}

.animatedFadeInUp {
    opacity: 0
}

.fadeInUp {
    opacity: 0;
    animation-name: fadeInUp;
    -webkit-animation-name: fadeInUp;
}

.ps__rail-y:hover > .ps__thumb-y {
    background-color: #999;
    width: 6px;
}

.ps__rail-y {
    width: 0; 
}


